body {
  margin: 0;
  display: flex;
  justify-content: center;
  min-height: 100vh;
  align-items: center;
  background-color: aliceblue;
  font-family: sans-serif;
}
.btn {
  background-color: pink;
  padding: 20px 40px;
  border-radius: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  text-decoration: none;
  color: black;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  border: none;
  font-size: 16px;
}
.btn::before {
  content: "";
  position: absolute;
  background-color: orangered;
  width: 0;
  height: 0;
  top: var(--rippleTop);
  left: var(--rippleLeft);
  transform: translate(-50%, -50%);
  border-radius: 50%;
  transition: width 500ms, height 500ms;
}
.btn:hover::before {
  width: 300px;
  height: 300px;
}
.btn span {
  position: relative;
  z-index: 1;
}
